@import "Global.scss";

nav {
    
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;

    background-color: $white20;
    backdrop-filter: blur(5px);
    box-shadow: $black 0px 10px 40px 0px;

    $padding: 20px;

    width: calc(100vw - calc(4 * $padding));
    margin: $padding;
    padding: $padding;

    border-radius: 12px;

    display: grid;

    @media screen and (max-width: 767px) {
        gap: 20px;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        padding-bottom: 0;

        img {
            place-self: center;
            grid-area: 1 / 2 / 2 / 3;
        }

        .links {
            grid-area: 2 / 1 / 3 / 4;
        }
    }

    @media screen and (min-width: 768px) {
        align-items: center;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;

        img {
            grid-area: 1 / 1 / 2 / 2;
        }

        .links {
            grid-area: 1 / 3 / 2 / 6;
            margin-right: 14px;
        }
    }

    img {
        margin: 0 !important;
        transition: 0.4s;

        &:hover {
            cursor: pointer;
            $skew: -10deg;
            transform: skewX($skew);

        }
    }

    .links {
        display: flex;
        flex-direction: row;
        place-content: center;
        justify-content: space-between;
        gap: 40px;

        h6 {
            display: grid;

            text-align: center;
            color: $white;
            transition: 0.4s;

            &:hover {
                cursor: pointer;
                $skew: -10deg;
                transform: skewX($skew);
            }
        }
    }
}