@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

* {
    margin: 0;
    font-family: 'Bebas Neue', cursive;
}

$primary: #345D68;
$primary60: #345D6860;
$secondary: #FFF3B1;
$black: #0E181B;
$white: #F9FAFB;
$white20: #F9FAFB20;

body {
    background-color: $black;
}

h1{
    z-index: 999;
    color: $white;
    font-weight: 400;
    text-align: center;
    transition: 0.6s;
    @media screen and (max-width: 767px) {
        font-size: 6rem;
    }
    @media screen and (min-width: 768px) {
        font-size: 16rem;
    }
    &:hover {
        $skew: -10deg;
        transform: skewX($skew);
    }
}

h6{
    z-index: 999;
    color: $white;
    font-weight: 400;

    @media screen and (max-width: 767px) {
        font-size: 1rem;
    }
    @media screen and (min-width: 768px) {  
        font-size: 1.5rem;
    }
}

.noise{
    filter: grayscale(100%);
    background-image: url(../../assets/img/noise.svg);
}


.space{
    height: 100vh;
    width: 100vw;
}

.innerShadow{
    box-shadow: inset 0px 0px 40px 40px $black;
}