@import "Global.scss";

.section {
    img {
        &.cover {
            place-content: center;
            transition: 0.4s;
            width: 100vw;
            height: 100vh;
        }
    }
    
    .cover {
        width: 100vw;
        height: 100vh;

        display: grid;
        align-items: center;

        transition: 0.4s;

        @media screen and (max-width: 767px) {
            grid-template-rows: repeat(5, 1fr);
            grid-template-columns: 1fr;
            text-align: center;

            .align-left {
                align-self: start;
                grid-area: 4 / 1 / 5 / 2;
            }

            .align-center {
                grid-area: 3 / 1 / 4 / 2;
            }

            .align-right {
                align-self: end;
                grid-area: 2 / 1 / 3 / 2;
            }
        }

        @media screen and (min-width: 768px) {
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: 1fr;

            .align-left {
                text-align: left;
            }

            .align-right {
                text-align: right;
            }
        }

    }
}