@import "Global.scss";

.centerContent {
    height: 100vh;
    width: 100vw;
    display: grid;
    place-items: center;
}

.logo {
    position: relative;
    top: 0;
    margin: 0;
    transition: 0.4s;
    width: 100%;
    height: 100%;
    max-width: 120px;
    max-height: 120px;
    margin: 20px;
    opacity: 1;
}


.content {
    .main {
        box-shadow: 0px 0px 40px 40px $black;
        z-index: 999;
        position: sticky;
        top: 0;
        padding: 0;

        @media screen and (max-width: 767px) {
            padding-top: 170.78px;
        }
        @media screen and (min-width: 768px) {
            padding-top: 125.39px;
        }

        background-color: $black;

        h2 {
            font-weight: 400;
            margin: 0;
            padding: 0;
            color: $primary;
            text-align: center;
            transition: 0.4s;
            opacity: 1;

            @media screen and (max-width: 767px) {
                font-size: 6rem;
            }
            @media screen and (min-width: 768px) {
                font-size: 16rem;
            }
        }
    }

    h3 {
        font-size: 8rem;
        font-weight: 400;
        margin: 0;
        padding: 0;
        color: $white;
        text-align: center;
        transition: 0.4s;
        opacity: 1;

        @media screen and (max-width: 767px) {
            font-size: 4rem;
        }
        @media screen and (min-width: 768px) {
            font-size: 8rem;
        }
    }
}